@import url(https://fonts.googleapis.com/css?family=Lobster|Raleway:400,500);
/*
    Variables, imports, and site wide styles
*/
body {
  font-family: "Raleway", sans-serif !important; }

a {
  color: #315931; }
  a:hover {
    color: #315931; }
  a:active {
    color: #315931; }
  a:visited {
    color: #315931; }

/*
    Navigation bar and contents
*/
nav.navbar {
  background-color: #315931; }
  nav.navbar .navbar-brand {
    font-family: 'Lobster', cursive;
    text-shadow: 2px 2px 0px #040704; }

/*
    Also navigation related, pagination re-color
*/
.pagination {
  margin-top: 2em; }
  .pagination .page-link {
    color: #315931; }
    .pagination .page-link:hover {
      color: #315931; }
  .pagination .page-item.active .page-link {
    background-color: #315931;
    border-color: #315931; }
  .pagination.hidden {
    display: none; }

/*
	The homepage search bar
*/
#main-search-form {
  margin-top: 1em;
  margin-bottom: 1em; }
  #main-search-form > input {
    border: 1px solid #315931;
    background-color: rgba(255, 255, 255, 0.25);
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding: 5px 15px;
    font-size: 20px;
    color: #315931 !important;
    line-height: 20px; }
  #main-search-form > button {
    font-family: 'Lobster', cursive;
    letter-spacing: .05em;
    border: 1px solid #315931;
    background-color: #315931;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding: 0px 7px 0px 0px;
    font-size: 20px;
    font-weight: light;
    color: white !important;
    line-height: 20px;
    cursor: pointer;
    text-shadow: 2px 2px 0px #040704;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-left: none; }
    #main-search-form > button:hover {
      background-color: rgba(255, 255, 255, 0.25);
      color: #315931 !important;
      text-shadow: 2px 2px 0px white; }
    #main-search-form > button:active {
      background-color: #315931;
      color: white !important;
      text-shadow: 2px 2px 0px #040704; }

/* When the screen gets to small they stack, restyle them so they look nice stacked */
@media (max-width: 575px) {
  #main-search-form {
    margin-left: 1em;
    margin-right: 1em; }
  #main-search-form > input {
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding: 10px; }
  #main-search-form > button {
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-left: 1px solid green;
    border-top: none;
    padding: 10px; } }

/*
    Search Message Styles and similars (used for notices and such as well, big and centered)
*/
.search-message, .no-visit-message, .visit-violation-summary {
  padding: .25em .75em 1em .75em;
  font-size: 22px;
  text-align: center; }
  .search-message .searching, .no-visit-message .searching, .visit-violation-summary .searching {
    font-style: italic; }

.no-visit-message, .visit-violation-summary {
  text-align: left; }

/*
    Search Results Styles
*/
.search-result:hover {
  background-color: #f8fbf8; }

.search-result a {
  text-decoration: none;
  color: black; }
  .search-result a:hover {
    color: #315931;
    text-decoration: none; }
    .search-result a:hover use.type-icon {
      stroke: #315931; }
    .search-result a:hover use.tier-number {
      fill: #315931; }
  .search-result a use.type-icon {
    stroke: black;
    fill: white; }
  .search-result a use.tier-number {
    font-family: 'Lobster', cursive;
    paint-order: stroke;
    stroke: white;
    stroke-width: 5;
    fill: black;
    font-size: 42px; }

.search-result h4 {
  display: inline;
  line-height: 1.75; }

.search-result h5 {
  display: inline;
  padding-left: .5em;
  font-style: italic; }

.search-result address {
  margin-bottom: .25em; }

.search-result svg {
  width: 3.75em;
  height: 3.75em;
  display: inline-block;
  overflow: hidden;
  margin: 0px 0px 0px 5px;
  stroke-width: 1.75;
  padding-top: .5em;
  margin-bottom: 0px; }

.search-result .violations {
  font-family: 'Lobster', cursive;
  font-size: 50px;
  text-align: center;
  margin-bottom: -10px;
  line-height: 35px;
  margin-top: 5px; }
  .search-result .violations i {
    font-size: 16px;
    font-style: normal;
    font-family: 'Raleway', sans-serif;
    display: block; }

/*
    Footer Styles
*/
#footer {
  margin: 2em auto 5em auto;
  font-size: .9em;
  line-height: 1.7em;
  text-align: center; }

/*
    Page styles of various types, starting with details
*/
table.table .date {
  white-space: nowrap; }

h1.fancy, h2.fancy, h3.fancy, h4.fancy, h5.fancy, h6.fancy {
  font-family: 'Lobster', cursive;
  color: #315931;
  margin-top: .75em; }

.readme-markdown h1, .readme-markdown h2, .readme-markdown h3, .readme-markdown h4, .readme-markdown h5, .readme-markdown h6 {
  font-family: 'Lobster', cursive;
  color: #315931;
  margin-top: .75em; }

p, li {
  font-size: 18px; }

.lic-orgname > h1 {
  margin: .25em 0em;
  padding: 0; }

.lic-details {
  margin: -.25em .5em -.25em .5em; }
  .lic-details > div {
    padding: .25em; }

.combine-link {
  margin-top: .75em;
  text-align: right; }

.btn-outline-primary {
  border-color: #315931 !important;
  color: #315931 !important; }
  .btn-outline-primary:hover {
    color: white !important;
    background-color: #315931; }
  .btn-outline-primary:active {
    color: white !important;
    background-color: #315931 !important; }

.license-traversal-btn .not-a-btn {
  margin: 1em -1px 0px;
  vertical-align: bottom;
  border-top: 1px solid #315931;
  border-bottom: 1px solid #315931;
  color: #315931;
  border-radius: unset; }

.license-traversal-btn #lic-back-b {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0; }

.license-traversal-btn #lic-forward-b {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }

.naughty-note {
  text-align: center;
  margin-top: 1em;
  margin-bottom: 1em; }

